.AdminPanel img{
    width: 100px;
    height: 100px;
    
}
.AdminPanel {
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    overflow: auto;
}
.AdminPanel h2 {
    text-align: center;
    text-decoration: underline;
    overflow: auto;
}
.AdminPanel TableHead{
    background-color: #f5f5f5;
    border-radius: 5px;
    padding: 10px;
    margin-bottom: 10px;
    overflow: auto;
    
}
.AdminPanel TableHead TableRow{
    text-align: center;
    text-decoration: underline;
    overflow: auto;
    position: fixed;
}

.AdminPanel .description {
  font-size: 14px;
  width: 228px;
  height: 80px;
  overflow: auto;
  
}